
import { Application, Sprite, Container, TickerCallback, Ticker } from 'pixi.js';

import Playfield from '../Playfield';
import GameView from '../GameView';
import Calculator from '../Calculator';
import Score from '../Score';

import { canvasConfig } from '../../configs/gameViewConfigs'

import type { ICanvas, Text } from 'pixi.js';

import type { IGame } from '../../ts/interfaces/game.interfaces';
import type { TGameInstances } from '../../ts/types/game.types';

class Game implements IGame {
    private canvas: Application<ICanvas>;
    private gameContainer: Container;
    private score: Score;
    private playfield: Playfield;
    private gameView: GameView;
    private calculator: Calculator;
    private gameIsNotOver = true;
    private tickerTileShiftAnimationCount = 0;

    public async initializing() {
        if (this.playfield === undefined) {
            this.createCanvas();
            this.createGameContainer();

            // Creating instances for game
            const score = new Score();
            const calculator = new Calculator(this, score);
            const playfield = new Playfield(this);
            const gameView = new GameView(this, playfield, score);

            this.score = score;
            this.calculator = calculator;
            this.playfield = playfield;
            this.gameView = gameView;

            await gameView.renderGameView();
        }
    }

    private createCanvas() {
        const { width, height  } = canvasConfig;

        // Creating a canvas instance
        this.canvas = new Application({ width, height, backgroundColor: '#ffffff' });
        // @ts-ignore
        document.getElementById('root').appendChild(this.canvas.view);
    }

    private createGameContainer() {
        this.gameContainer = new Container();
        // Property for arranging sprites along the z-axis
        this.gameContainer.sortableChildren = true;
        this.canvas.stage.addChild(this.gameContainer);
    }

    public async finishTheGame(win: boolean) {
        this.gameIsNotOver = false;
        this.removeAllElementsFromGame();
        await this.gameView.renderEndOfGame(win);
    }

    public addElementToGame(element: Sprite | Text) {
        this.gameContainer.addChild(element);
    }

    public removeElementFromGame(element: Sprite | Text) {
        this.gameContainer.removeChild(element);
    }

    private removeAllElementsFromGame() {
        while(this.gameContainer.children[0]) {
            this.gameContainer.removeChild(this.gameContainer.children[0]);
        }
    }

    public startAnimation(callback: TickerCallback<any>) {
        const ticker = this.createAndStartTicker(callback);
        return ticker;
    }

    public stopAnimation(ticker: Ticker) {
        this.stopAndDestroyTicker(ticker);
    }

    public startTileShiftAnimation(callback: TickerCallback<any>) {
        const ticker = this.createAndStartTicker(callback);
        this.tickerTileShiftAnimationCount++;
        return ticker;
    }

    public stopTileShiftAnimation(ticker: Ticker) {
        this.stopAndDestroyTicker(ticker);

        setTimeout(() => {
            this.tickerTileShiftAnimationCount--;
            if (this.tickerTileShiftAnimationCount === 0 && this.gameIsNotOver) {
                this.playfield.doEndMoveDownTiles();
            }
        }, 100);
    }

    private createAndStartTicker(callback: TickerCallback<any>): Ticker {
        const ticker = new Ticker();
        ticker.add(callback);
        ticker.start();
        return ticker;
    }

    private stopAndDestroyTicker(ticker: Ticker) {
        ticker.stop();
        ticker = null;
    }

    public get getGameIsNotOver() {
        return this.gameIsNotOver;
    }

    public get instances(): TGameInstances {
        return {
            score: this.score,
            calculator: this.calculator,
            playfield: this.playfield,
            gameView: this.gameView,
        };
    }
}

const game = new Game();

export default game;