
import { Text, TextStyle } from 'pixi.js';

import { styleConfig } from '../../configs/textConfigs';
import { textConfig } from '../../configs/gameViewConfigs';

import type { ITextCreator } from '../../ts/interfaces/text.interfaces';

class TextCreator implements ITextCreator {
    public getMovesTextObj(text: number) {
        return this.getTextObj(
            text,
            styleConfig.defaultFontColor,
            textConfig.movesFontSize
        );
    }

    public getResultTextObj(text: string) {
        return this.getTextObj(text, textConfig.resultFontColor);
    }

    public getTextObj(
        text: number | string,
        defaultFontColor = styleConfig.defaultFontColor,
        fontSize = styleConfig.defaultFontSize
    ) {
        const style = new TextStyle({
            fontFamily: styleConfig.fontFamily,
            fill: defaultFontColor,
            fontSize
        });

        const textObj = new Text(text, style);

        return textObj;
    }
}

export default TextCreator;