import Tile from '../Tile';
import SpriteCreator from '../SpriteCreator';
import TextCreator from '../TextCreator';

import { backgroundsConfig, textConfig, tileConfig } from '../../configs/gameViewConfigs';
import { zIndexConfig } from '../../configs/spritesConfigs';

import { SpriteNames } from '../../ts/enums/sprite.enums';

import type { Text } from 'pixi.js';

import type gameInstance from '../Game';
import type Playfield from '../Playfield';
import type Score from '../Score';

import type { IGameView } from '../../ts/interfaces/view.interfaces';
import type { TTilesArray } from '../../ts/types/playfield.types';

class GameView implements IGameView {
    private game: typeof gameInstance;
    private playfield: Playfield;
    private score: Score;
    private spriteCreator: SpriteCreator;
    private textCreator: TextCreator;
    private scoreTextObj: Text;
    private movesTextObj: Text;

    constructor(game: typeof gameInstance, playfield: Playfield, score: Score) {
        this.game = game;
        this.playfield = playfield;
        this.score = score;

        this.spriteCreator = new SpriteCreator();
        this.textCreator = new TextCreator();

        this.scoreTextObj = this.textCreator.getTextObj(this.score.getScore);
        this.movesTextObj = this.textCreator.getMovesTextObj(this.score.getMoves);
    }

    public async renderGameView() {
        await this.renderMainBackground();
        await this.renderPlayfieldBackground();
        await this.renderGamePlayfield();
        this.renderScore();
        this.renderMoves();
        this.renderGameRules();
    }

    private async renderMainBackground() {
        const mainBgSprite = await this.spriteCreator.getSprite(SpriteNames.MAIN_BACKGROUND);
        // z-axis background position
        mainBgSprite.zIndex = zIndexConfig.mainBgSprite;
        this.game.addElementToGame(mainBgSprite);
    }

    private async renderPlayfieldBackground() {
        const { playfieldBgPositionX, playfieldBgPositionY } = backgroundsConfig;

        const playfieldBgSprite = await this.spriteCreator.getSprite(SpriteNames.PLAYFIELD_BACKGROUND);
        // Coordinates on the canvas for playfield background
        playfieldBgSprite.x = playfieldBgPositionX;
        playfieldBgSprite.y = playfieldBgPositionY;
        // z-axis background position
        playfieldBgSprite.zIndex = zIndexConfig.playfieldBgSprite;
        this.game.addElementToGame(playfieldBgSprite);
    }

    public async renderGamePlayfield() {
        const { startTilePositionY } = tileConfig;
        const gamePlayfiled = await this.playfield.getGamePlayfield();

        this.arrayWalkthroughAndRenderingTiles(gamePlayfiled, startTilePositionY);
    }

    private renderScore() {
        const { scorePositionX, scorePositionY } = textConfig;

        this.scoreTextObj.anchor.set(0.5);
        this.scoreTextObj.x = scorePositionX;
        this.scoreTextObj.y = scorePositionY;
        this.scoreTextObj.zIndex = zIndexConfig.text;

        this.game.addElementToGame(this.scoreTextObj);
    }

    private renderMoves() {
        const { movesPositionX, movesPositionY } = textConfig;

        this.movesTextObj.anchor.set(0.5);
        this.movesTextObj.x = movesPositionX;
        this.movesTextObj.y = movesPositionY;
        this.movesTextObj.zIndex = zIndexConfig.text;

        this.game.addElementToGame(this.movesTextObj);
    }

    private renderGameRules() {
        const { gameRulesText, gameRulesPositionX, gameRulesPositionY } = textConfig;

        const rulesTextObj = this.textCreator.getTextObj(gameRulesText);

        rulesTextObj.x = gameRulesPositionX;
        rulesTextObj.y = gameRulesPositionY;
        rulesTextObj.zIndex = zIndexConfig.text;

        this.game.addElementToGame(rulesTextObj);
    }

    public async renderEndOfGame(win: boolean) {
        const {
            winText, lossText,
            emojiPositionX, emojiPositionY,
            resultTextPositionX, resultTextPositionY,
            finalScoreText, finalScoreTextPositionX, finalScoreTextPositionY,
            refreshGameText, refreshGameTextPositionX, refreshGameTextPositionY,
        } = textConfig;

        const emojiSpriteName = win ? SpriteNames.WIN_EMOJI : SpriteNames.LOSS_EMOJI;
        const emojiSprite = await this.spriteCreator.getSprite(emojiSpriteName);
        emojiSprite.x = emojiPositionX;
        emojiSprite.y = emojiPositionY;

        const resultText = win ? winText : lossText;
        const resultTextObj = this.textCreator.getResultTextObj(resultText);
        resultTextObj.x = resultTextPositionX;
        resultTextObj.y = resultTextPositionY;

        const fullFinalScoreText = `${finalScoreText} ${this.scoreTextObj.text}`;
        const finalScoreTextObj = this.textCreator.getResultTextObj(fullFinalScoreText);
        finalScoreTextObj.anchor.set(0.5);
        finalScoreTextObj.x = finalScoreTextPositionX;
        finalScoreTextObj.y = finalScoreTextPositionY;

        const refreshGameTextObj = this.textCreator.getResultTextObj(refreshGameText);
        refreshGameTextObj.x = refreshGameTextPositionX;
        refreshGameTextObj.y = refreshGameTextPositionY;

        this.game.addElementToGame(emojiSprite);
        this.game.addElementToGame(resultTextObj);
        this.game.addElementToGame(finalScoreTextObj);
        this.game.addElementToGame(refreshGameTextObj);
    }

    public updateScore(score: number) {
        this.scoreTextObj.text = score;
    }

    public updateMoves(moves: number) {
        this.movesTextObj.text = moves;
    }

    public renderNewTiles(newTilesArr: TTilesArray) {
        const { startNewTilePositionY } = tileConfig;

        this.arrayWalkthroughAndRenderingTiles(newTilesArr, startNewTilePositionY);
    }

    private arrayWalkthroughAndRenderingTiles(tilesArray: TTilesArray, startTilePositionY: number) {
        const { startTilePositionX, tileOffsetY, tileOffsetX } = tileConfig;

        tilesArray.forEach((row, rowIndex) => {
            const tileSpriteCoordY = startTilePositionY + rowIndex * tileOffsetY;

            row.forEach((cellValue, cellValueIndex) => {
                if (cellValue instanceof Tile) {
                    const tile = cellValue;
                    const tileIndex = cellValueIndex;

                    const tileSprite = tile.getSprite;
                    // Coordinates on the canvas for current tile sprite
                    tileSprite.x = startTilePositionX + tileIndex * tileOffsetX;
                    tileSprite.y = tileSpriteCoordY;

                    this.game.addElementToGame(tileSprite);
                    tile.startTileAppearance();
                }
            })
        })
    }
}

export default GameView;