
import Tile from '../Tile';

import { mainConfig } from '../../configs/mainConfig';
import { neighborTilesLocationData } from '../../constants/tiles';

import { NumberDirection, Sign } from '../../ts/enums/calculator.enums';
import { TileTypes } from '../../ts/enums/tile.enums';

import type gameInstance from '../Game';
import type Score from '../Score';

import type { StringDirection } from '../../ts/enums/calculator.enums';
import type { ICalculator } from '../../ts/interfaces/calculator.interfaces';
import type { TCoords, TTilesArray } from '../../ts/types/playfield.types';
import type { TNeighborTileData } from '../../ts/types/tile.types';
import type {
    TCurrentTileData,
    TCurrentTileWithNeighbors,
    TDataToMoveTheTilesDown,
    TTilesTypesArray
} from '../../ts/types/calculator.types';

class Calculator implements ICalculator {
    private game: typeof gameInstance;
    private score: Score;
    /*
        Game playfield with tiles
     */
    private gamePlayfiled: TTilesArray | TTilesTypesArray;
    /*
        Tile from which the calculation was started
     */
    private initialTile: Tile;
    /*
        The current tile for which the calculations take place
     */
    private currentTile: TCurrentTileData;
    /*
        Array with the locations of neighboring tiles for the current tile
     */
    private neighborTilesLocation: Array<StringDirection> = [];
    /*
        Array coordinates of the tiles that are in contact with the initialTile
     */
    private neighborTilesCoords: Array<TCoords> = [];
    /*
        Array of tiles that have more than one neighbor
     */
    private tilesWithManyNeighborsData: Array<TCurrentTileWithNeighbors> = [];
    /*
        An object for storing neighbor tile positions data on the canvas.
        Needed to move the top tiles after removing neighbor tiles
     */
    private dataToMoveTheTilesDown: TDataToMoveTheTilesDown = {};

    constructor(game: typeof gameInstance, score: Score) {
        this.game = game;
        this.score = score;
    }

    /*
        Start of calculation
     */
    public async initiateCalculation(tile: Tile) {
        await this.initializing();
        this.initializingCurrentTile(tile);

        if (tile.type === TileTypes.BOMB) {
            this.calculateBombTiles();
        } else {
            this.calculateColorTiles();
        }
    }

    private async initializing() {
        this.gamePlayfiled = await this.game.instances.playfield.getGamePlayfield();
        this.resetPropertiesForTileCounting();
    }

    private initializingCurrentTile(tile: Tile) {
        this.initialTile = tile;

        const tileData: TCurrentTileData = {
            coordY: tile.playfieldCoords.y,
            coordX: tile.playfieldCoords.x,
            type: tile.type
        };
        this.currentTile = tileData;
    }

    private resetPropertiesForTileCounting() {
        this.resetNeighborTilesLocation();
        this.resetNeighborTilesCoords();
        this.tilesWithManyNeighborsData = [];
        this.dataToMoveTheTilesDown = {};
    }

    private resetNeighborTilesLocation() {
        this.neighborTilesLocation = [];
    }

    private resetNeighborTilesCoords() {
        this.neighborTilesCoords = [];
    }

    /*
        Recursive method in which methods for counting color tiles are called
     */
    private calculateColorTiles(reverseDirectionIndex: NumberDirection = 0) {
        // Check neighboring tiles. The passed value of reverseDirectionIndex on the first function call is 0
        this.checkTilesNearby(reverseDirectionIndex);
        /*
            First, it checks if neighboring tiles were found for the current tile.
            Then if no neighboring tiles were found, check if there were tiles with more than one neighbor.
            Then if there were no such tiles, it checks whether there were enough neighboring tiles
            (the number is specified in calculatorConfig)
         */
        if (this.checkIfThereAreNeighborTilesLocations()) {
            this.checkInitialTile();

            const reverseDirectionIndex = this.checkNeighborTile();
            this.calculateColorTiles(reverseDirectionIndex);
        } else if (this.checkTilesWithManyNeighborsDataIsNotEmpty()) {
            this.setTileWithManyNeighborsLikeCurrentTileData();

            const reverseDirectionIndex = this.checkNeighborTile();
            this.calculateColorTiles(reverseDirectionIndex);
        } else if (this.checkTilesAreEnough()) {
            this.game.instances.playfield.removeTiles(this.neighborTilesCoords, this.dataToMoveTheTilesDown);
            this.score.calculateScore(this.neighborTilesCoords.length);
        }
    }

    /*
        Method for checking the possibility of burning tiles
     */
    public initiateCheckPlayfield(playfield: TTilesArray): boolean {
        const playfieldCopy: TTilesTypesArray = [];

        playfield.forEach((row, rowIndex) => {
            playfieldCopy[rowIndex] = []
            row.forEach((tile, tileIndex) => {
                const tileData: TCurrentTileData = {
                    coordY: tile.playfieldCoords.y,
                    coordX: tile.playfieldCoords.x,
                    type: tile.type
                };

                playfieldCopy[rowIndex][tileIndex] = tileData;
            })
        })
        this.gamePlayfiled = playfieldCopy;

        if (this.checkPlayfieldHasBomb(playfieldCopy)) {
            return true;
        }

        this.resetPropertiesForTileCounting();

        let thereAreTilesToRemove = false;
        let playfieldCoordY = 0;

        this.currentTile = this.gamePlayfiled[0][0];
        let playfieldValue = this.currentTile;

        while (!thereAreTilesToRemove && playfieldValue) {
            this.gamePlayfiled[playfieldValue.coordY][playfieldValue.coordX] = null;
            this.resetNeighborTilesCoords();

            thereAreTilesToRemove = this.checkThereAreTilesToRemove();

            if (!thereAreTilesToRemove) {
                for (let row = playfieldCoordY; row < mainConfig.tilesNumberY; row++) {
                    playfieldValue = this.gamePlayfiled[row].find((cellValue) => cellValue);

                    playfieldCoordY = row;
                    if (playfieldValue) {
                        this.currentTile = playfieldValue;
                        break;
                    }
                }
            }
        }

        return thereAreTilesToRemove;
    }

    /*
        Method for checking the current tile for enough neighbors
     */
    private checkThereAreTilesToRemove(reverseDirectionIndex: NumberDirection = 0): boolean {
        this.checkTilesNearby(reverseDirectionIndex);

        if (this.checkTilesAreEnough()) {
            return true;
        }

        if (this.checkIfThereAreNeighborTilesLocations()) {
            const reverseDirectionIndex = this.checkNeighborTile();
            return this.checkThereAreTilesToRemove(reverseDirectionIndex);
        } else if (this.checkTilesWithManyNeighborsDataIsNotEmpty()) {
            this.setTileWithManyNeighborsLikeCurrentTileData();

            const reverseDirectionIndex = this.checkNeighborTile();
            return this.checkThereAreTilesToRemove(reverseDirectionIndex);
        } else {
            return false;
        }
    }

    private checkPlayfieldHasBomb(playfieldCopy: TTilesTypesArray): boolean {
        let bombFound = false;

        for (let row of playfieldCopy) {
            if (bombFound) {
                break;
            }
            for (let tileData of row) {
                if (tileData.type === TileTypes.BOMB) {
                    bombFound = true;
                    break;
                }
            }
        }

        return bombFound;
    }

    /*
        Recursive method for calculating tiles to remove after activating a bomb tile
        @TODO Simplify method
     */
    private calculateBombTiles(radius = mainConfig.bombRadius) {
        if (radius === 0) {
            this.game.instances.playfield.removeTiles(this.neighborTilesCoords, this.dataToMoveTheTilesDown);
            this.score.calculateScore(this.neighborTilesCoords.length, true);
            return;
        }

        if (radius === mainConfig.bombRadius) {
            this.doRecordTileDataInCalculateBombTilesMethod();
        }

        const { coordY, coordX } = this.currentTile;
        const initialTileCoords = {
            y: coordY,
            x: coordX
        };

        let y = coordY - radius;
        let x = coordX - radius;
        if (this.checkRowExist(y)) {
            while (x <= coordX + radius) {
                if (this.checkTileExist({ y, x })) {
                    this.setCurrentTileCoords({ y, x });
                    this.doRecordTileDataInCalculateBombTilesMethod();
                }

                x++;
            }
        }

        y = coordY - radius + 1;
        x = coordX + radius;
        while (!this.checkRowExist(y) && y < coordY) {
            y++;
        }
        if (this.checkTileExist({ y, x })) {
            this.setCurrentTileCoords({ y, x });
            this.doRecordTileDataInCalculateBombTilesMethod();

            y++;
            while (y <= coordY + radius) {
                if (this.checkRowExist(y) && this.checkTileExist({ y, x })) {
                    this.setCurrentTileCoords({ y, x });
                    this.doRecordTileDataInCalculateBombTilesMethod();
                }

                y++;
            }
        }

        y = coordY + radius;
        x = coordX + radius - 1;
        if (this.checkRowExist(y)) {
            while (x >= coordX - radius) {
                if (this.checkTileExist({ y, x })) {
                    this.setCurrentTileCoords({ y, x });
                    this.doRecordTileDataInCalculateBombTilesMethod();
                }

                x--;
            }
        }

        y = coordY + radius - 1;
        x = coordX - radius;
        while (!this.checkRowExist(y) && y > coordY) {
            y--;
        }
        if (this.checkTileExist({ y, x })) {
            this.setCurrentTileCoords({ y, x });
            this.doRecordTileDataInCalculateBombTilesMethod();

            y--;
            while (y >= coordY - radius) {
                if (this.checkRowExist(y) && this.checkTileExist({ y, x })) {
                    this.setCurrentTileCoords({ y, x });
                    this.doRecordTileDataInCalculateBombTilesMethod();
                }

                y--;
            }
        }

        this.setCurrentTileCoords(initialTileCoords);
        this.calculateBombTiles(radius - 1);
    }

    private doRecordTileDataInCalculateBombTilesMethod() {
        this.recordCurrentTileCoords();
        this.recordCurrentTilePositionOnCanvas();
    }

    /*
        Method for checking if there are similar tiles next to each other
     */
    private checkTilesNearby(reverseDirectionIndex: NumberDirection) {
        if (this.checkIfThereAreNeighborTilesLocations()) {
            this.resetNeighborTilesLocation();
        }

        const neighborTilesDataArr = Object.values(neighborTilesLocationData);

        /*
            Exclusion of checking the neighboring tile in the direction from which we came
         */
        if (reverseDirectionIndex) {
            const spliceStartIndex = reverseDirectionIndex - 1;
            neighborTilesDataArr.splice(spliceStartIndex, 1);
        }

        this.checkNeighborTileIsTheSame(neighborTilesDataArr, reverseDirectionIndex);
    }

    /*
        Recursive method of checking a certain neighboring tile.
        Analyzing the neighborTilesDataArr direction array by the neighborTilesDataArrIndex (equal to 0 on first call)
     */
    private checkNeighborTileIsTheSame(
        neighborTilesDataArr: Array<TNeighborTileData>,
        direction?: NumberDirection,
        neighborTilesDataArrIndex = 0
    ) {
        const { coordY, coordX, type: tileType } = this.currentTile;
        const neighborTileData = neighborTilesDataArr[neighborTilesDataArrIndex];
        const { location } = neighborTileData;

        const { y: tileCoordY , x: tileCoordX } = this.getNeighborTileCoords(coordY, coordX, neighborTileData);
        /*
            First, check for the existence of a value on the Y-axis.
            Then check if the value is an instance of the Tile class, and if so, if the type the same.
         */
        if (
            this.checkRowExist(tileCoordY) &&
            this.checkTileExist({ y: tileCoordY, x: tileCoordX }) &&
            this.gamePlayfiled[tileCoordY][tileCoordX].type === tileType
        ) {
            this.neighborTilesLocation.unshift(location);
        }

        const nextIndex = neighborTilesDataArrIndex + 1;
        if (neighborTilesDataArr.length === nextIndex) {
            return;
        } else {
            this.checkNeighborTileIsTheSame(neighborTilesDataArr, direction, nextIndex);
        }
    }

    /*
        Method for checking the data of neighboring tile
     */
    private checkNeighborTile(): NumberDirection {
        // Getting the location of the neighbor tile
        const neighborTileLocation = this.getNeighborTileLocation();
        // Getting location data
        const neighborTileLocationData = neighborTilesLocationData[neighborTileLocation];
        // If there are still neighbors' locations left, write down the tile data
        if (this.checkIfThereAreNeighborTilesLocations()) {
            this.recordTileWithManyNeighborsData();
        }

        const { coordY, coordX } = this.currentTile;
        const neighborTileCoords = this.getNeighborTileCoords(coordY, coordX, neighborTileLocationData);

        if (this.checkTileExist(neighborTileCoords)) {
            this.setCurrentTileCoords(neighborTileCoords);

            this.recordCurrentTilePositionOnCanvas();
            this.recordCurrentTileCoords();
        }

        return this.getReverseDirection(NumberDirection[neighborTileLocation]);
    }

    private setTileWithManyNeighborsLikeCurrentTileData() {
        const { coordY, coordX, neighborLocation } = this.getTileWithManyNeighborsData();
        this.currentTile.coordY = coordY;
        this.currentTile.coordX = coordX;

        this.neighborTilesLocation = neighborLocation;
    }

    private setCurrentTileCoords(coords: TCoords) {
        const { y, x } = coords;

        this.currentTile.coordY = y;
        this.currentTile.coordX = x;
    }

    private getNeighborTileCoords(coordY: number, coordX: number, neighborTileData: TNeighborTileData ): TCoords {
        const { xAxis, sign } = neighborTileData;

        const isSignPlus = sign === Sign.PLUS;
        /*
            (For tileCoordX: Y-axis and X-coordinate)
            For tileCoordY: If checking tiles on the X-axis, the Y-coordinate remains,
            otherwise, depending on the sign, one is subtracted or added
            to view the nearby tile
         */
        const y = xAxis ? coordY : isSignPlus ? coordY + 1 : coordY - 1;
        const x = !xAxis ? coordX : isSignPlus ? coordX + 1 : coordX - 1;

        return { y, x };
    }

    private getTileWithManyNeighborsData() {
        return this.tilesWithManyNeighborsData.pop();
    }

    private getNeighborTileLocation(): StringDirection {
        return this.neighborTilesLocation.pop();
    }


    private getReverseDirection(direction: NumberDirection): NumberDirection {
        switch (direction) {
            case NumberDirection.UP:
                return NumberDirection.DOWN;
            case NumberDirection.RIGHT:
                return NumberDirection.LEFT;
            case NumberDirection.DOWN:
                return NumberDirection.UP;
            case NumberDirection.LEFT:
                return NumberDirection.RIGHT;
        }
    }

    private recordCurrentTileCoords() {
        const { coordY, coordX } = this.currentTile;

        const coords = {
            y: coordY,
            x: coordX
        }
        this.neighborTilesCoords.push(coords);
        // hanging the value of the tile in the playing field
        this.gamePlayfiled[coordY][coordX] = null;
    }

    /*
        Method for checking and recording data about the position of the current tile on the canvas
    */
    private recordCurrentTilePositionOnCanvas() {
        const { coordY: playfieldCoordY, coordX: playfieldCoordX } = this.currentTile;
        /*
            @TODO Change comment
            First, we check if the data object has a key with the value of the column number.
            If such a key exists, then we check if the tileSpriteCoordY coordinate of the current
            tile is greater than lowerTileSpriteCoordY coordinate. If the coordinate is larger, we
            change the value, if not we don't. In both cases we increase numberOfTiles by 1.
            If there is no key with value of column number, create such a key and write data of
            current tile in its value.
         */
        if (playfieldCoordX in this.dataToMoveTheTilesDown) {
            const {
                lowerPlayfieldCoordY,
                numberOfTiles,
                ...otherProperties
            } = this.dataToMoveTheTilesDown[playfieldCoordX];

            if (playfieldCoordY > lowerPlayfieldCoordY) {
                this.dataToMoveTheTilesDown[playfieldCoordX] = {
                    numberOfTiles: numberOfTiles + 1,
                    lowerPlayfieldCoordY: playfieldCoordY,
                    ...otherProperties
                }
            } else {
                this.dataToMoveTheTilesDown[playfieldCoordX] = {
                    numberOfTiles: numberOfTiles + 1,
                    lowerPlayfieldCoordY,
                    ...otherProperties
                }
            }
        } else {
            this.dataToMoveTheTilesDown[playfieldCoordX] = {
                playfieldCoordX,
                lowerPlayfieldCoordY: playfieldCoordY,
                numberOfTiles: 1
            };
        }
    }

    private recordTileWithManyNeighborsData() {
        const { coordY, coordX } = this.currentTile;

        const tileData: TCurrentTileWithNeighbors = {
            coordY: coordY,
            coordX: coordX,
            neighborLocation: this.neighborTilesLocation
        };
        this.tilesWithManyNeighborsData.push(tileData);
    }

    private checkInitialTile() {
        const { coordY, coordX } = this.currentTile;
        if (
            this.checkCurrentTileIsInitialTile() &&
            this.checkTileExist({ y: coordY, x: coordX })
        ) {
            this.recordCurrentTilePositionOnCanvas();
            this.recordCurrentTileCoords();
        }
    }

    private checkCurrentTileIsInitialTile(): boolean {
        const { playfieldCoords: { y, x } } = this.initialTile;
        const { coordY, coordX } = this.currentTile;

        return coordY === y && coordX === x;
    }

    private checkRowExist(coordY: number): boolean {
        return this.gamePlayfiled[coordY] !== undefined;
    }

    private checkTileExist(coords: TCoords): boolean {
        const { y, x } = coords;

        const cellValue = this.gamePlayfiled[y][x];
        return Boolean(cellValue);
    }

    private checkTilesAreEnough(): boolean {
        return this.neighborTilesCoords.length >= mainConfig.tilesNearby;
    }

    private checkTilesWithManyNeighborsDataIsNotEmpty(): boolean {
        return Boolean(this.tilesWithManyNeighborsData[0]);
    }

    private checkIfThereAreNeighborTilesLocations(): boolean {
        return this.neighborTilesLocation.length > 0;
    }
}

export default Calculator;