
import Tile from '../Tile';
import BombTile from '../BombTile';
import Shuffle from '../Shuffle';

import { mainConfig } from '../../configs/mainConfig';
import { getTileTypeNameByIndex } from '../../utils/tile';
import { getRandomNumber } from '../../utils/general/number';

import { TileTypes } from '../../ts/enums/tile.enums';

import type { Sprite } from 'pixi.js';

import type gameInstance from '../Game';

import type { IPlayfield } from '../../ts/interfaces/playfield.interfaces';
import type { TCoords, TTilesArray, TPlayfields } from '../../ts/types/playfield.types';
import type { TDataToMoveTheTilesDown } from '../../ts/types/calculator.types';

class Playfield implements IPlayfield {
    private game: typeof gameInstance;
    readonly tilesNumberX: number;
    readonly tilesNumberY: number;
    readonly tilesArrayRowLength: number;
    private shuffle: Shuffle;
    private insideGamePlayfield: TTilesArray;
    private outsideGamePlayfield: TTilesArray;
    private newTilesArray: TTilesArray;
    private lastTileCoordsToRemove: TCoords;
    private dataToMoveTheTilesDown: TDataToMoveTheTilesDown;

    constructor(game: typeof gameInstance) {
        this.game = game;
        this.tilesNumberX = mainConfig.tilesNumberX;
        this.tilesNumberY = mainConfig.tilesNumberY;
        this.tilesArrayRowLength = mainConfig.tilesNumberY - 1;
        this.shuffle = new Shuffle();
    }

    public async getGamePlayfield() {
        if (this.outsideGamePlayfield === undefined) {
            await this.initializePlayfields()
        }

        return this.outsideGamePlayfield;
    }

    private async initializePlayfields() {
        let correctPlayfiled = false;

        /*
            Cycle to generate a starting playing field with the ability to burn tiles
         */
        while (!correctPlayfiled) {
            const { insidePlayfieldArr, outsidePlayfieldArr } = await this.createPlayfields();

            correctPlayfiled = this.game.instances.calculator.initiateCheckPlayfield(insidePlayfieldArr);

            if (correctPlayfiled) {
                this.insideGamePlayfield = insidePlayfieldArr;
                this.outsideGamePlayfield = outsidePlayfieldArr;
            }
        }
    }

    private async createPlayfields(): Promise<TPlayfields> {
        const insidePlayfieldArr: Tile[][] = [];
        const outsidePlayfieldArr: Tile[][] = [];

        for (let coordY = 0; coordY < this.tilesNumberY; coordY++) {
            insidePlayfieldArr[coordY] = [];
            outsidePlayfieldArr[coordY] = [];

            for (let coordX = 0; coordX < this.tilesNumberX; coordX++) {
                const playfieldCoords = { y: coordY, x: coordX };
                const tile = await this.getTile(playfieldCoords);

                insidePlayfieldArr[coordY][coordX] = tile;
                outsidePlayfieldArr[coordY][coordX] = tile;
            }
        }

        return {
            insidePlayfieldArr,
            outsidePlayfieldArr
        };
    }

    public async removeTiles(tilesCoords: Array<TCoords>, dataToMoveTheTilesDown: TDataToMoveTheTilesDown) {
        // Disabling the interactivity of tiles that cannot be removed
        this.disableInteractivityOfTiles();

        await this.prepareToReplaceRemovedTiles(tilesCoords, dataToMoveTheTilesDown);

        tilesCoords.forEach((coords) => {
            const tile = this.insideGamePlayfield[coords.y][coords.x];
            // When the disappearance animation ends, the remove function is triggered
            tile.startTileDisappearance(this.removeTile.bind(this));
        });
    }

    private removeTile(coords: TCoords, sprite: Sprite) {
        /*
            First remove the tile sprite from canvas.
            Then remove the tile instance from insideGamePlayfield.
         */
        this.game.removeElementFromGame(sprite);
        this.removeDataOfTile(this.insideGamePlayfield, coords);

        if (this.checkRemovedTileIsLast(coords)) {
            this.movingTopTilesDown();
        }
    }

    private async prepareToReplaceRemovedTiles(tilesCoords: Array<TCoords>, dataToMoveTheTilesDown: TDataToMoveTheTilesDown) {
        // Recording the data to start the downward movement of tiles
        this.dataToMoveTheTilesDown = dataToMoveTheTilesDown;
        this.setLastTileCoordsToRemove(tilesCoords[tilesCoords.length - 1]);

        await this.createNewTilesArray();
    }

    private async createNewTilesArray() {
        const newTilesArr: TTilesArray = [];

        for (let y = 0; y < this.tilesNumberY; y++) {
            newTilesArr[y] = [];
            for (let x = 0; x < this.tilesNumberX; x++) {
                newTilesArr[y][x] = null;
            }
        }

        for (let coordX = 0; coordX < this.tilesNumberX; coordX++) {
            if (coordX in this.dataToMoveTheTilesDown) {
                const { numberOfTiles } = this.dataToMoveTheTilesDown[coordX];

                let newTilesCount = 0;
                let coordY = this.tilesArrayRowLength;

                while (newTilesCount !== numberOfTiles) {
                    const playfieldCoords = { y: coordY, x: coordX };
                    const tile = await this.getTile(playfieldCoords);

                    tile.disableTileIntercativeSettings();

                    newTilesArr[coordY][coordX] = tile;

                    newTilesCount++;
                    coordY--;
                }
            }
        }

        this.newTilesArray = newTilesArr;

        if (this.game.getGameIsNotOver) {
            await this.game.instances.gameView.renderNewTiles(newTilesArr);
        }
    }

    private async getTile(playfieldCoords: TCoords) {
        const { rangeForBombTile, numberOfBombTile, numberOfTileColors } = mainConfig;

        const indexForBombTile = getRandomNumber(rangeForBombTile);

        let tile: Tile | BombTile;
        if (indexForBombTile === numberOfBombTile) {
            const bombType = getTileTypeNameByIndex(indexForBombTile);
            tile = new BombTile(TileTypes[bombType], playfieldCoords);
        } else {
            const colorIndex = getRandomNumber(numberOfTileColors);
            const colorType = getTileTypeNameByIndex(colorIndex);
            tile = new Tile(TileTypes[colorType], playfieldCoords);
        }

        await tile.initializeTileSprite();

        return tile;
    }

    private movingTopTilesDown() {
        for (const key in this.dataToMoveTheTilesDown) {
            const {
                playfieldCoordX,
                lowerPlayfieldCoordY,
                numberOfTiles
            } = this.dataToMoveTheTilesDown[key];

            let numberOfRemovedTiles = 0;
            let newTilesArrayCoordY = this.tilesArrayRowLength;

            for (
                let playfieldCoordY = lowerPlayfieldCoordY;
                playfieldCoordY >= 0;
                playfieldCoordY--
            ) {
                // Check and move the tiles that are on the playing field
                const cellInsideGamePlayfieldValue = this.insideGamePlayfield[playfieldCoordY][playfieldCoordX];

                if (cellInsideGamePlayfieldValue instanceof Tile) {
                    const tile = cellInsideGamePlayfieldValue;
                    tile.startMoveDownTile(numberOfRemovedTiles);

                    const playfieldCoords = { y: playfieldCoordY, x: playfieldCoordX };
                    this.updateOldTilesInPlayfields(tile, playfieldCoords, numberOfRemovedTiles);
                } else {
                    numberOfRemovedTiles++;
                }
                // Moving created new tiles
                const cellNewTilesArrayValue = this.newTilesArray[newTilesArrayCoordY][playfieldCoordX];

                if (cellNewTilesArrayValue instanceof Tile) {
                    const newTile = cellNewTilesArrayValue;
                    newTile.startMoveDownTile(numberOfTiles, true);
                }

                newTilesArrayCoordY--;
            }

            this.moveNewTilesToPlayfields(key);
        }
    }

    private updateOldTilesInPlayfields(tile: Tile, coords: TCoords, yAxisOffset: number) {
        const { y: playfieldCoordY, x: playfieldCoordX } = coords;

        const newPlayfieldCoordY = playfieldCoordY + yAxisOffset;
        tile.playfieldCoords.y = newPlayfieldCoordY;

        this.insideGamePlayfield[newPlayfieldCoordY][playfieldCoordX] = tile;
        this.outsideGamePlayfield[newPlayfieldCoordY][playfieldCoordX] = tile;

        this.removeDataOfTile(this.insideGamePlayfield, coords);
        this.removeDataOfTile(this.outsideGamePlayfield, coords);
    }

    private moveNewTilesToPlayfields(key: string) {
        const { numberOfTiles, playfieldCoordX } = this.dataToMoveTheTilesDown[key];

        let tilesCount = numberOfTiles;
        let newTilesArrayCoordY = this.tilesArrayRowLength;

        while (tilesCount > 0) {
            const cellNewTilesArrayValue = this.newTilesArray[newTilesArrayCoordY][playfieldCoordX];

            if (cellNewTilesArrayValue instanceof Tile) {
                const newTile = cellNewTilesArrayValue;

                const tileCoords = { y: newTilesArrayCoordY, x: playfieldCoordX };

                const newTilePositionInPlayfieldY = tilesCount - 1;
                newTile.playfieldCoords.y = newTilePositionInPlayfieldY;

                this.insideGamePlayfield[newTilePositionInPlayfieldY][playfieldCoordX] = newTile;
                this.outsideGamePlayfield[newTilePositionInPlayfieldY][playfieldCoordX] = newTile;

                this.removeDataOfTile(this.newTilesArray, tileCoords);
            }
            tilesCount--;
            newTilesArrayCoordY--;
        };
    }

    /*
        Method which the playing field is checked for the possibility of burning tiles. If there is no such
        possibility, the field is shuffle. The number of shuffles equals numberOfShuffle
     */
    public doEndMoveDownTiles() {
        let playfieldCorrect = false;
        let firstTime = true;
        let shuffleCount = 0;

        while (!playfieldCorrect) {
            if (!firstTime) {
                shuffleCount++;

                if (shuffleCount > mainConfig.numberOfShuffle) {
                    this.game.finishTheGame(false);
                    break;
                }

                this.shuffle.shuffleGamePlayfields(this.insideGamePlayfield, this.outsideGamePlayfield);
            }

            playfieldCorrect = this.game.instances.calculator.initiateCheckPlayfield(this.insideGamePlayfield);

            if (firstTime && !playfieldCorrect) {
                this.insideGamePlayfield.forEach((row) => {
                    row.forEach((tile) => {
                        this.game.removeElementFromGame(tile.getSprite);
                    })
                })

                firstTime = false;
            } else if (playfieldCorrect) {
                if (firstTime) {
                    this.enableInteractivityOfTiles();
                } else {
                    this.game.instances.gameView.renderGamePlayfield();
                    this.enableInteractivityOfTiles();
                }
            }
        }
    }

    private removeDataOfTile(tilesArray: TTilesArray, tileCoords: TCoords) {
        const { y, x } = tileCoords;

        tilesArray[y][x] = null;
    }

    private disableInteractivityOfTiles() {
        this.outsideGamePlayfield.forEach((row) => {
            row.forEach((cellValue) => {
                if (cellValue instanceof Tile) {
                    cellValue.disableTileIntercativeSettings();
                }
            });
        });
    }

    private enableInteractivityOfTiles() {
        this.outsideGamePlayfield.forEach((row) => {
            row.forEach((cellValue) => {
                if (cellValue instanceof Tile) {
                    cellValue.enableTileInteractiveSettings();
                }
            });
        });
    }

    private setLastTileCoordsToRemove(coords: TCoords) {
        this.lastTileCoordsToRemove = coords;
    }

    private checkRemovedTileIsLast(currentTileCoords: TCoords): boolean {
        const { y: currentTileCoordY, x: currentTileCoordX } = currentTileCoords;
        const { y: lastTileCoordY, x: lastTileCoordX } = this.lastTileCoordsToRemove;

        return currentTileCoordY === lastTileCoordY && currentTileCoordX === lastTileCoordX;
    }
}

export default Playfield;
