import gameInstance from '../Game';

import { scoreConfig } from '../../configs/scoreConfigs';

import type { IScore } from '../../ts/interfaces/score.interfaces';

class Score implements IScore {
    private score: number;
    private moves: number;

    constructor() {
        this.score = 0;
        this.moves = scoreConfig.maxMoves;
    }

    public calculateScore(tilesCount: number, isBomb = false) {
        let points = this.scoringForRemovedTiles(tilesCount);
        if (isBomb) {
            points += scoreConfig.bombBonus;
        }
        this.updateScoreInfo(points);
    }

    private updateScoreInfo(points: number) {
        this.score += points;
        gameInstance.instances.gameView.updateScore(this.score);
        if (this.score >= scoreConfig.victoryPoints) {
            gameInstance.finishTheGame(true);
            return;
        }

        this.moves--;
        if (this.moves === 0) {
            gameInstance.finishTheGame(false);
        } else {
            gameInstance.instances.gameView.updateMoves(this.moves);
        }
    }

    public get getScore() {
        return this.score;
    }

    public get getMoves() {
        return this.moves;
    }

    /*
        The basic method of scoring for removed tiles
     */
    private scoringForRemovedTiles(tilesCount: number) {
        const { oneTileValue } = scoreConfig;

        if (tilesCount < 1) {
            return 0;
        }
        if (tilesCount === 1) {
            return oneTileValue;
        }

        return oneTileValue * Math.pow(tilesCount, 2);
    }
}

export default Score;