import { getErrorMsg } from '../general/error';

import { TileTypes } from '../../ts/enums/tile.enums';
import { SpriteNames } from '../../ts/enums/sprite.enums';
import { tileErrorMsg } from '../../constants/errors/tileError';
import { tileConfig } from '../../configs/gameViewConfigs';

const getTileTypeNameByIndex = (index: number): TileTypes => {
    switch (index) {
        case 1:
            return TileTypes.BLUE;
        case 2:
            return TileTypes.GREEN;
        case 3:
            return TileTypes.PURPLE;
        case 4:
            return TileTypes.RED;
        case 5:
            return TileTypes.YELLOW;
        case 6:
            return TileTypes.BOMB;
        default:
            const errorMsg = tileErrorMsg.utils.getTileTypeNameByIndex.UNKNOWN_INDEX;
            throw new Error(getErrorMsg(errorMsg, index));
    }
};

const getTileNameByType = (type: TileTypes): SpriteNames => {
    switch (type) {
        case TileTypes.BLUE:
            return SpriteNames.BLUE_TILE;
        case TileTypes.GREEN:
            return SpriteNames.GREEN_TILE;
        case TileTypes.PURPLE:
            return SpriteNames.PURPLE_TILE;
        case TileTypes.RED:
            return SpriteNames.RED_TILE;
        case TileTypes.YELLOW:
            return SpriteNames.YELLOW_TILE;
        case TileTypes.BOMB:
            return SpriteNames.BOMB_TILE;
        default:
            const errorMsg = tileErrorMsg.utils.getTileNumberByColor.UNKNOWN_TYPE;
            throw new Error(getErrorMsg(errorMsg, type));
    }
}

const getStepDown = () => {
    const divider = 9;
    return tileConfig.tileOffsetY / divider;
}

export {
    getTileTypeNameByIndex,
    getTileNameByType,
    getStepDown
}