
import { Assets, Sprite } from 'pixi.js';

import { SpriteNames } from '../../ts/enums/sprite.enums';
import { imagesMap } from '../../constants/images';

import type { ISpriteCreator } from '../../ts/interfaces/sprite.interfaces';

class SpriteCreator implements ISpriteCreator {
   public async getSprite(imgName: SpriteNames) {
        const image = imagesMap.get(imgName);

        if (image === undefined) {
            throw new Error('Unknown image');
        }

        const texture = await Assets.load(image);
        const sprite = new Sprite(texture);
        return sprite;
    }
}

export default SpriteCreator;