
import SpriteCreator from '../SpriteCreator';

import gameInstance from '../Game';

import { getTileNameByType, getStepDown } from '../../utils/tile';
import { tileConfig } from '../../configs/gameViewConfigs';
import { zIndexConfig } from '../../configs/spritesConfigs';

import type { Sprite, Ticker } from 'pixi.js';

import type Calculator from '../Calculator';

import type { TileTypes } from '../../ts/enums/tile.enums';
import type { ITile } from '../../ts/interfaces/tile.interfaces';
import type { TCoords, TRemoveTileFunc } from '../../ts/types/playfield.types';

class Tile implements ITile {
    private sprite: Sprite;
    readonly type: TileTypes;
    readonly playfieldCoords: TCoords;
    private removeTile: TRemoveTileFunc;
    readonly stepDown: number;
    private targetPositionY: number;
    private animationTicker: Ticker;
    readonly calculator: Calculator;

    constructor(type: TileTypes, playfieldCoords: TCoords) {
        this.type = type;
        this.playfieldCoords = playfieldCoords;
        this.stepDown = getStepDown();

        this.calculator = gameInstance.instances.calculator;
    }

    public async initializeTileSprite() {
        const spriteCreator = new SpriteCreator();

        const tileName = getTileNameByType(this.type);
        const sprite = await spriteCreator.getSprite(tileName);

        this.sprite = sprite;
        this.sprite.alpha = 0.3;
        // z-axis tile position
        this.sprite.zIndex = zIndexConfig.tileSprite;

        this.enableTileInteractiveSettings();
        this.setTileEvents();
    }

    public get getSprite() {
        return this.sprite;
    }

    public enableTileInteractiveSettings() {
        if (!this.sprite.interactive) {
            this.sprite.interactive = true;
            this.sprite.cursor = 'pointer';
        }
    }

    public disableTileIntercativeSettings() {
        if (this.sprite.interactive) {
            this.sprite.interactive = false;
            this.sprite.cursor = 'auto';
        }
    }

    private setTileEvents() {
        this.sprite.on('pointertap', async () => {
            await this.calculator.initiateCalculation(this);
        });
    }

    public startTileAppearance() {
        this.animationTicker = gameInstance.startAnimation(this.tileAppearance.bind(this));
    }

    private tileAppearance() {
        this.sprite.alpha += tileConfig.tileStepAlpha;
        if (this.sprite.alpha > 1) {
            gameInstance.stopAnimation(this.animationTicker);
        }
    }

    public startTileDisappearance(removeTile?: TRemoveTileFunc) {
        if (typeof removeTile === 'function') this.removeTile = removeTile;

        this.animationTicker = gameInstance.startTileShiftAnimation(this.tileDisappearance.bind(this));
    }

    private tileDisappearance() {
        this.sprite.alpha -= tileConfig.tileStepAlpha;
        if (this.sprite.alpha < 0) {
            gameInstance.stopTileShiftAnimation(this.animationTicker);

            if (typeof this.removeTile === 'function') this.removeTile(this.playfieldCoords, this.sprite);
        }
    }

    public startMoveDownTile(numberOfTiles: number, newTile?: boolean) {
        const { tileOffsetY, pixelsForStartPositionY } = tileConfig;

        this.targetPositionY = this.sprite.y + numberOfTiles * tileOffsetY;
        if (newTile) {
            this.targetPositionY += pixelsForStartPositionY;
        }

        this.animationTicker = gameInstance.startTileShiftAnimation(this.moveDownTile.bind(this));
    }

    private moveDownTile() {
        this.sprite.y += this.stepDown;
        if (this.sprite.y >= this.targetPositionY) {
            gameInstance.stopTileShiftAnimation(this.animationTicker);
        }
    }
}

export default Tile;