
import type Tile from '../Tile';

import type { TTilesArray } from '../../ts/types/playfield.types';
import type { IShuffle } from '../../ts/interfaces/shuffle.interfaces';

class Shuffle implements IShuffle {
    private bufferArray: Tile[];
    private insideGamePlayfield: TTilesArray;
    private outsideGamePlayfield: TTilesArray;

    public shuffleGamePlayfields(insideGamePlayfield: TTilesArray, outsideGamePlayfield: TTilesArray) {
        this.insideGamePlayfield = insideGamePlayfield;
        this.outsideGamePlayfield = outsideGamePlayfield;

        this.fillBufferArrayWithTilesData();
        this.shuffleBufferArray();
        this.setTilesDataFromBufferArrayToPlayfields();
    }

    private fillBufferArrayWithTilesData() {
        this.bufferArray = [];
        this.insideGamePlayfield.forEach((row) => {
            row.forEach((tile: Tile) => {
                this.bufferArray.push(tile);
            })
        })
    }

    /*
        FisherYates shuffle
     */
    private shuffleBufferArray() {
        for (let i = this.bufferArray.length - 1; i > 0; i--) {
            let j = Math.floor(Math.random() * (i + 1));
            [this.bufferArray[i], this.bufferArray[j]] = [this.bufferArray[j], this.bufferArray[i]];
        }
    }

    private setTilesDataFromBufferArrayToPlayfields() {
        let i = 0;
        for (let y = 0; y < this.insideGamePlayfield.length; y++) {
            for (let x = 0; x < this.insideGamePlayfield[y].length; x++) {
                const tile = this.bufferArray[i];
                tile.playfieldCoords.y = y;
                tile.playfieldCoords.x = x;

                this.insideGamePlayfield[y][x] = tile;
                this.outsideGamePlayfield[y][x] = tile;
                i++;
            }
        }
    }
}

export default Shuffle